body[data-animations="true"] application-window {
  -webkit-animation: show .3s;
  -moz-animation: show .3s;
  -ms-animation: show .3s;
  animation: show .3s;
  -webkit-transition: height 0.3s ease-in-out, width 0.3s, top 0.3s, left 0.3s;
  -moz-transition: height 0.3s ease-in-out, width 0.3s, top 0.3s, left 0.3s;
  -ms-transition: height 0.3s ease-in-out, width 0.3s, top 0.3s, left 0.3s;
  -o-transition: height 0.3s ease-in-out, width 0.3s, top 0.3s, left 0.3s;
  transition: height 0.3s ease-in-out, width 0.3s, top 0.3s, left 0.3s;
}
body[data-animations="true"] application-window[data-hint="resizing"],
body[data-animations="true"] application-window[data-hint="moving"] {
  -webkit-transition: none;
  -moz-transition: none;
  -ms-transition: none;
  -o-transition: none;
  transition: none;
}
body[data-animations="true"] application-window[data-hint="closing"],
body[data-animations="true"] application-window[data-minimized="true"] {
  -webkit-animation: hide .3s;
  -moz-animation: hide .3s;
  -ms-animation: hide .3s;
  animation: hide .3s;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  -ms-animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
body[data-animations="true"] .WindowDropRect {
  -webkit-animation: pulse 2s linear infinite;
  -moz-animation: pulse 2s linear infinite;
  -ms-animation: pulse 2s linear infinite;
  animation: pulse 2s linear infinite;
  border-image: initial;
}
body[data-animations="true"] .WindowAttentionBlink {
  -webkit-animation: attention 2s linear infinite;
  -moz-animation: attention 2s linear infinite;
  -ms-animation: attention 2s linear infinite;
  animation: attention 2s linear infinite;
}
body[data-animations="true"][data-attention="true"]:after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  -webkit-animation: pulseback 2s linear infinite;
  -moz-animation: pulseback 2s linear infinite;
  -ms-animation: pulseback 2s linear infinite;
  animation: pulseback 2s linear infinite;
}
body[data-animations="true"] corewm-panel {
  -webkit-transition: translate 0.3s ease-in-out;
  -moz-transition: translate 0.3s ease-in-out;
  -ms-transition: translate 0.3s ease-in-out;
  -o-transition: translate 0.3s ease-in-out;
  transition: translate 0.3s ease-in-out;
}
body[data-animations="true"] corewm-panel .Button.Attention {
  -webkit-animation: attention 2s linear infinite;
  -moz-animation: attention 2s linear infinite;
  -ms-animation: attention 2s linear infinite;
  animation: attention 2s linear infinite;
}
body[data-animations="true"] gui-menu,
body[data-animations="true"] corewm-notification-entry {
  -webkit-animation: fadeIn .3s;
  -moz-animation: fadeIn .3s;
  -ms-animation: fadeIn .3s;
  animation: fadeIn .3s;
}
body[data-animations="true"] gui-menu[data-hint="closing"],
body[data-animations="true"] corewm-notification[data-hint="closing"] {
  -webkit-animation: fadeOut .3s;
  -moz-animation: fadeOut .3s;
  -ms-animation: fadeOut .3s;
  animation: fadeOut .3s;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  -ms-animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
